                aria-labelledby="nav-home-tab">
                <div class="row ml-0 mt-3">
                  <div class="col-6 col-md-4 pl-0 con" v-for="(i, index) in tempData.photo" :key="index">
                    <input type="radio" name="img-radio" :value="i"
                      :id="'img-option-'+index" v-model="formData.photo">
                    <label :for="'img-option-'+index" :style="{'background-image': `url(${i})`}">
                    </label>                    
                  </div>
                </div>
              </div>
              <div class="tab-pane fade" id="nav-host" role="tabpanel"
                aria-labelledby="nav-profile-tab">
                <label for="file-input" class="files">
                  <img src="../../assets/img/bg/images-select.svg" alt="音樂封面選擇" 
                    v-if="!tempDom.imgurl || errors.has('photo')" class="select-img">
                  <div class="img-preview" v-else>
                    <img :src="tempDom.imgurl" id="scream"/>
                  </div>                  
                  <input type="file" name="photo" id="file-input"
                    multiple="true" accept="image/*" @change="preview"
                    v-validate="'size:5000'" data-vv-as="圖片檔案">
                  <p class="text-danger mt-1 small" v-if="errors.has('photo')">
                    請選擇小於 5MB 的圖片
                  </p>
