<script>
import firebase from 'firebase';
import $ from 'jquery';

export default {
  name: 'creatShow',
  data() {
    return {
      isLoading: false,
      userData: {},
      formData: {
        bio: '',
        title: '',
        city: '',
        type: [],
      },
      tempDom: {
        alldataDone: false,
        filterItem: ['活動表演', '公益活動', '指導教學', '商業表演','其他'],
        selecttype: [],
        imgurl: null,
        tempImg: '',
        errorInput: '',
        elseMailOpened: 'default',
      },
      tempData: {
        photo: [
          'https://firebasestorage.googleapis.com/v0/b/meason-music.appspot.com/o/default_photo%2F1.jpg?alt=media&token=6e69c9cd-1c73-4932-a1e5-d11a1646baa6',
          'https://firebasestorage.googleapis.com/v0/b/meason-music.appspot.com/o/default_photo%2F4.jpg?alt=media&token=affdc7ae-4540-46d9-927b-fff7aec4d40b',
          'https://firebasestorage.googleapis.com/v0/b/meason-music.appspot.com/o/default_photo%2F5.jpg?alt=media&token=9700dcb4-8ee6-4758-bb93-18410344f94b',
          'https://firebasestorage.googleapis.com/v0/b/meason-music.appspot.com/o/default_photo%2F6.jpg?alt=media&token=df5d1da3-cb40-41c1-81c3-ec2d1768a688',
          'https://firebasestorage.googleapis.com/v0/b/meason-music.appspot.com/o/default_photo%2F7.jpg?alt=media&token=8565624a-0ccc-4c70-ae05-12c7926cae3d',
          'https://firebasestorage.googleapis.com/v0/b/meason-music.appspot.com/o/default_photo%2F8.jpg?alt=media&token=e14031c9-44d7-4d3d-b3ec-c22d11aace09',
          'https://firebasestorage.googleapis.com/v0/b/meason-music.appspot.com/o/default_photo%2F9.jpg?alt=media&token=2d73919b-4da4-4fc4-800e-be3a1da8beca',
          'https://firebasestorage.googleapis.com/v0/b/meason-music.appspot.com/o/default_photo%2F10.jpg?alt=media&token=d49e932d-e3e6-45de-9f0c-1bc79d209400',
          'https://firebasestorage.googleapis.com/v0/b/meason-music.appspot.com/o/default_photo%2F11.jpg?alt=media&token=14c4b5fa-87d6-4528-af74-d354fd7c1504',
          'https://firebasestorage.googleapis.com/v0/b/meason-music.appspot.com/o/default_photo%2F12.jpg?alt=media&token=b25d5b6f-603f-46bb-ade0-c2624f241459',
          'https://firebasestorage.googleapis.com/v0/b/meason-music.appspot.com/o/default_photo%2F15.jpg?alt=media&token=80f80646-ca80-44e4-8f70-ca8d2a32e54e',
          'https://firebasestorage.googleapis.com/v0/b/meason-music.appspot.com/o/default_photo%2F16.jpg?alt=media&token=e9579979-4b8b-4e78-81bd-8ba10d8b77bd',
          'https://firebasestorage.googleapis.com/v0/b/meason-music.appspot.com/o/default_photo%2F17.jpg?alt=media&token=476f544e-afec-4933-895c-81f370b3990a',
          'https://firebasestorage.googleapis.com/v0/b/meason-music.appspot.com/o/default_photo%2F18.jpg?alt=media&token=86a09d8f-7120-48eb-b81a-3f2685fa2383',
          'https://firebasestorage.googleapis.com/v0/b/meason-music.appspot.com/o/default_photo%2F13.jpg?alt=media&token=87ec4e49-c076-47c9-a2a3-b4110d171bf0',
          'https://firebasestorage.googleapis.com/v0/b/meason-music.appspot.com/o/default_photo%2F14.jpg?alt=media&token=674e5da0-3693-4fbe-9bb9-16280c3ca17b',
          'https://firebasestorage.googleapis.com/v0/b/meason-music.appspot.com/o/default_photo%2F2.jpg?alt=media&token=87a34eaa-1c8b-4d12-845a-e9041b787401',
          'https://firebasestorage.googleapis.com/v0/b/meason-music.appspot.com/o/default_photo%2F3.jpg?alt=media&token=d3f1a26d-d001-4afc-a142-fa7e3e5ad62a',
        ],
        options: ['活動表演', '公益活動', '指導教學', '商業表演','其他'],
      },
      errorMsg: '',
    }
  },
  watch: {
    'tempDom.selecttype': {
      handler: function selecttype(val) {
        const vm = this;
        vm.formData.type = [];
        val.forEach(function(item, index){
          if(item) {
            vm.formData.type.push(vm.tempData.options[index])
          }
        });
      }
    },
    'formData.bio': {
      handler: function autogrow(val) {
        const textarea = document.getElementById('bio');
        let adjustedHeight = textarea.clientHeight;
        adjustedHeight = Math.max(textarea.scrollHeight,adjustedHeight);
        if (adjustedHeight > textarea.clientHeight) {
          textarea.style.height=adjustedHeight+'px';
        }
      },
    },
  },
  methods: {
    preview(e) {
      const vm = this;
      const file = e.target.files[0];
      vm.tempDom.imgurl = URL.createObjectURL(file);
    },
    cvs() {
      const vm = this;
      let imgNewWidth = 400, // 圖片新寬度
      canvas = document.createElement("canvas"),
      img = new Image(),
      context = canvas.getContext("2d");
      
      let imgfile = $('#scream'),
      imgH = imgfile.height(),
      imgW = imgfile.width(),
      imgNewHeight = imgNewWidth * imgH / imgW, // 圖片新高度
      newImg

      img.src = vm.tempDom.imgurl;
      canvas.width = imgNewWidth;
      canvas.height = imgNewHeight;
      context.clearRect(0, 0, imgNewWidth, imgNewHeight);
      context.drawImage(img, 0, 0, imgNewWidth, imgNewHeight); // 調整圖片尺寸
      newImg = canvas.toDataURL("image/jpeg", 0.9);
      return newImg
    },
    uploadImg() {
      const vm = this;
      vm.isLoading = true;
      if(!vm.formData.photo) { //如果不是選預設圖在上傳
        var timestamp = Number(new Date()); // 檔名設定
        let fileName = timestamp.toString() + vm.userData.uid;
        var storageRef = firebase.storage().ref('show_photo/' + fileName); //資料夾+黨名
        var file = vm.dataURLtoFile(vm.cvs(), 'fileName');
        storageRef.put(file)
        .then((snapshot) => {
          // 取得圖片位置
          snapshot.ref.getDownloadURL().then(function(downloadURL) {
            vm.formData.photo = downloadURL;
            $('#upload-img').modal('hide');
            vm.isLoading = false;
          });
        });
      } else {
        $('#upload-img').modal('hide');
        vm.isLoading = false;
      }
    },
    dataURLtoFile(dataurl, filename) {
      var arr = dataurl.split(','), mime = arr[0].match(/:(.*?);/)[1],
        bstr = atob(arr[1]), n = bstr.length, u8arr = new Uint8Array(n);
      while(n--){
        u8arr[n] = bstr.charCodeAt(n);
      }
      return new File([u8arr], filename, {type:mime});
    },
    getUserData() {
      const vm = this;
      const url = `${process.env.VUE_APP_API}/getData`;
      firebase.auth().onAuthStateChanged(function(user) {
        if (user) {
          vm.$http.post(url, {"url": `users/${user.uid}/basic/`}).then((response) => {
            vm.userData = response.data;
            vm.userData.uid =  user.uid;
          })
        } else {
          vm.userData = null;
        }
      });
    },
    creatPost() {
      const vm = this;
      this.$validator.validate().then((result) => {
        if (result) {
          // do stuff if not valid.          
          if (!vm.formData.type[0]) {
            vm.tempDom.errorInput = 'type';
          } else if(!vm.formData.photo) {
            vm.tempDom.errorInput = 'photo';
          } else {
            // 確保有登入
            vm.tempDom.alldataDone = true;
            if(vm.userData){
              vm.sendPostAPI();
            } else {
              alert('會員登入狀況出現錯誤，請重新登入');
              vm.$router.push('/show');
            }
          }
        } else {
          console.log('欄位不完整')
        }
      });
    },
    sendPostAPI() {
      const vm = this;
      vm.isLoading = true;
      const url = `${process.env.VUE_APP_API}/creatShow`;
      const dd = new Date();
      const newformData = {
        bio: vm.formData.bio,
        date: `${dd.getFullYear()}-${dd.getMonth()+1}-${dd.getDate()}`,
        timestamp: Date.now(),
        display: true,
        legal: true,
        type: vm.formData.type,
        picURL: vm.formData.photo,
        poster_email: vm.formData.poster_email,
        poster_uid: vm.userData.uid,
        title: vm.formData.title,
        type: vm.formData.type,
        city: vm.formData.city,
        uid: '',
        show_date: vm.formData.show_date,
      }
      if(vm.tempDom.elseMailOpened === 'default') {
        newformData.poster_email = vm.userData.email;
      }
      this.$http.post(url, {"formData": newformData}).then((response) => {
        vm.isLoading = false;
        vm.$router.push('/show');
      })
      .catch(function(error){
        console.error("寫入錯誤",error);
        alert('發佈失敗，請稍後再重試，或聯繫迷聲客服中心');
        vm.isLoading = false;
      });
    }
  },
  created() {
    const vm = this;
    vm.getUserData();
    window.onbeforeunload = (e) => {
      e.returnValue = '確定要離開嗎？系統不會保存目前內容';
    };
  },
  beforeRouteLeave (to, from , next) {
    if(!this.tempDom.alldataDone) {
      const answer = window.confirm('確定要離開嗎？系統不會保存目前內容')
      if (answer) {
        next()
      } else {
        next(false)
      }
    } else {
      next()
    }
  }
}
</script>

<style lang="scss" scoped>
textarea{
  resize: none;
  &::-webkit-scrollbar {
    width: 0px;
  }
  &::-webkit-scrollbar-track {
    background: transparent
  }
  &::-webkit-scrollbar-thumb {
    background-color: transparent;
